___CSS_LOADER_EXPORT___.push([module.id, "\n.demo-app-main {\n  padding: 30px;\n}\n.width-all {\n  left: 0% !important;\n  right: 0% !important;\n  z-index: 99999 !important;\n}\n.base {\n  border-radius: 0px;\n  border: none;\n}\n.base {\n  border-radius: 0px;\n  border: none;\n}\n.warning {\n  background: #f8ecda;\n  border-left: 3px solid #e6a23c;\n}\n.success {\n  background: #e4fad1;\n  border-left: 3px solid #67c23a;\n}\n.danger {\n  background: #f9e3e2;\n  border-left: 3px solid #f56c6c;\n}\n.info {\n  background: #e9e9eb;\n  border-left: 3px solid #909399;\n}\n", "",{"version":3,"sources":["webpack://src/DemoApp.vue"],"names":[],"mappings":";AA6KA;EACA,aAAA;AACA;AAEA;EACA,mBAAA;EACA,oBAAA;EACA,yBAAA;AACA;AAEA;EACA,kBAAA;EACA,YAAA;AACA;AAEA;EACA,kBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,8BAAA;AACA;AAEA;EACA,mBAAA;EACA,8BAAA;AACA;AAEA;EACA,mBAAA;EACA,8BAAA;AACA;AAEA;EACA,mBAAA;EACA,8BAAA;AACA","sourcesContent":["<template>\n  <div class=\"demo-app-main\">\n    <!-- 自定义切换周 -->\n    <div style=\"marginbottom: 20px\">\n      <span>这是自定义按钮：</span>\n      <button @click=\"qiehuanZhou('prev')\">上一周</button>\n      <button @click=\"qiehuanZhou('next')\">下一周</button>\n      <button @click=\"qiehuanZhou('today')\">当周</button>\n    </div>\n\n    <FullCalendar\n      ref=\"fullCalendar\"\n      class=\"demo-app-calendar\"\n      :options=\"calendarOptions\"\n    ></FullCalendar>\n  </div>\n</template>\n\n<script>\nimport FullCalendar from \"@fullcalendar/vue\";\nimport timeGridPlugin from \"@fullcalendar/timegrid\";\nimport cnLocale from \"@fullcalendar/core/locales/zh-cn\";\n\nimport moment from \"moment\";\nimport tippy from \"tippy.js\";\nimport \"tippy.js/dist/tippy.css\";\nimport { comp2Dom } from \"./util\";\nimport Tooltip from \"./Tooltip.vue\";\n\nexport default {\n  components: {\n    FullCalendar, // make the <FullCalendar> tag available\n  },\n\n  data: function () {\n    return {\n      calendarOptions: {\n        plugins: [\n          timeGridPlugin, // timeGridWeek插件\n        ],\n        allDaySlot: false, // 显示all-day\n        headerToolbar: false, // 显示头部工具栏\n        initialView: \"timeGridWeek\", // 视图\n        initialEvents: [\n          {\n            id: 100,\n            title: \"测试\",\n            classNames: \"base warning\",\n            textColor: \"#333\",\n            start: Date.now(),\n            end: Date.now() + 3600000 * 3,\n            extendedProps: {\n              时间: \"07:00\",\n              地点: \"现场\",\n              类型: \"HR面试\",\n              职位: \"前端开发\",\n              候选人: \"马明哲\",\n              面试官: \"你、我、他、她、它\",\n            },\n          },\n          {\n            id: 101,\n            title: \"测试2\",\n            classNames: \"base danger\",\n            textColor: \"#333\",\n            start: Date.now(),\n            end: Date.now() + 3600000 * 2,\n            extendedProps: {\n              时间: \"07:00\",\n              地点: \"现场2\",\n              类型: \"HR面试2\",\n              职位: \"前端开发2\",\n              候选人: \"马明哲2\",\n              面试官: \"你、我、他、她、它\",\n            },\n          },\n          {\n            id: 102,\n            title: \"测试3\",\n            classNames: \"base info\",\n            textColor: \"#333\",\n            start: Date.now(),\n            end: Date.now() + 3600000 * 1,\n            extendedProps: {\n              时间: \"07:00\",\n              地点: \"现场3\",\n              类型: \"HR面试3\",\n              职位: \"前端开发3\",\n              候选人: \"马明哲3\",\n              面试官: \"你、我、他、她、它\",\n            },\n          },\n          {\n            id: 103,\n            title: \"测试4\",\n            classNames: \"base success\",\n            textColor: \"#333\",\n            start: Date.now(),\n            end: Date.now() + 3600000 * 4,\n            extendedProps: {\n              时间: \"07:00\",\n              地点: \"现场4\",\n              类型: \"HR面试4\",\n              职位: \"前端开发4\",\n              候选人: \"马明哲4\",\n              面试官: \"你、我、他、她、它\",\n            },\n          },\n        ], // 视图事件（数据）\n        slotEventOverlap: false, // 视图事件互相重叠\n        firstDay: 1, // 周起始日（1 = 周一）\n        locale: cnLocale, // 语言\n\n        dayHeaderFormat: {\n          weekday: \"short\",\n          month: \"numeric\",\n          day: \"numeric\",\n          omitCommas: true,\n        }, // 顶部表头format\n        slotDuration: \"00:15:00\", // 侧边栏时间间隔\n        slotLabelInterval: \"01:00\", // 侧边栏显示间隔\n        slotMinTime: \"00:00:00\", // 侧边栏时间轴初试时间\n        slotMaxTime: \"24:00:00\", // 侧边栏时间轴结束时间\n        scrollTime: \"08:00:00\", // 滚动条默认定位时间\n        // 侧边栏format\n        slotLabelFormat(info) {\n          const date = moment(\"00:00:00\", \"HH:mm\")\n            .add(info.date.hour, \"hours\")\n            .add(info.date.minute, \"minutes\");\n          return date.format(\"HH:mm\");\n        },\n\n        // 视图事件鼠标移入\n        eventMouseEnter(info) {\n          info.el.parentNode.classList.add(\"width-all\");\n        },\n\n        // 视图事件鼠标移出\n        eventMouseLeave(info) {\n          info.el.parentNode.classList.remove(\"width-all\");\n        },\n\n        // 视图事件挂载调用\n        eventDidMount: function (info) {\n          info.el.parentNode.style.transition = \"0.3s all\";\n          tippy(info.el.parentNode, {\n            content: comp2Dom(Tooltip, {\n              props: {\n                data: info.event.extendedProps,\n              },\n            }),\n            allowHTML: true, // 使用html\n            interactive: true, // 有交互（鼠标移入弹框操作）\n            arrow: false, // 显示弹框箭头\n            delay: [300, null], // [隐藏延时, 显示延时]\n          });\n        },\n      },\n    };\n  },\n\n  methods: {\n    // 切换周\n    qiehuanZhou(funcName) {\n      const calendar = this.$refs.fullCalendar.getApi();\n\n      calendar[funcName]();\n    },\n  },\n};\n</script>\n\n<style lang=\"css\">\n.demo-app-main {\n  padding: 30px;\n}\n\n.width-all {\n  left: 0% !important;\n  right: 0% !important;\n  z-index: 99999 !important;\n}\n\n.base {\n  border-radius: 0px;\n  border: none;\n}\n\n.base {\n  border-radius: 0px;\n  border: none;\n}\n\n.warning {\n  background: #f8ecda;\n  border-left: 3px solid #e6a23c;\n}\n\n.success {\n  background: #e4fad1;\n  border-left: 3px solid #67c23a;\n}\n\n.danger {\n  background: #f9e3e2;\n  border-left: 3px solid #f56c6c;\n}\n\n.info {\n  background: #e9e9eb;\n  border-left: 3px solid #909399;\n}\n</style>\n"],"sourceRoot":""}]);
